.services-stages {
  margin: 7.5rem 0 0.5rem 0;

  @media (min-width: 1024px) {
    margin: 7.5rem 0 3.5rem 0;
  }

  h2 {
    max-width: 39.5rem;
    align-self: flex-start;
    margin-bottom: 4rem;

    @media (min-width: 1024px) {
      max-width: 61.5rem;
      margin-bottom: 8rem;
    }
  }

  &__inner {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    // @media (min-width: 768px) {
    //   flex-direction: row;
    //   justify-content: space-between;
    //   flex-wrap: wrap;
    //   align-items: initial;
    // }

    @media (min-width: 1024px) {
      max-width: 100%;
    }
  }

  &__cards {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    counter-reset: number 0;

    @media (min-width: 768px) {
      width: 73.8rem;
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
      align-items: initial;
    }

    @media (min-width: 1200px) {
      width: 110.8rem;
    }

    @media (min-width: 1400px) {
      width: 100%;
    }

    .services-stages__card-wrap {
      position: relative;
      overflow: hidden;
      counter-increment: number 1; 
      // width: 100%;
      width: 36rem;
      height: 23rem;
      margin-bottom: 2.5rem;
      background: var(--white_2);
      border-radius: 0.5rem;
      cursor: pointer;

      &::before {
        position: absolute;
        z-index: -1;
        content: "";
        transform: translateY(-50%) rotate(1deg);
        bottom: 0;
        right: 1.5rem;
        width: 1.2rem;
        height: 1rem;
        color: #fff;
        background: url("../img/icons/arrow.svg") 100% 100%/1.2rem 1rem no-repeat;
        will-change: transform;
        transition: transform .5s linear;
  
        @media (min-width: 500px) {
          width: 2.7rem;
          height: 2.7rem;
        }
      }

      &--active {
        height: 100%;
        overflow: auto;
      }

      &::after {
        position: absolute;
        z-index: 10;
        top: 1.5rem;
        right: 1.5rem;
        // counter-increment: number 1; 
        content: '0' counter(number);
        width: 4.1rem;
        height: 4.1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        @include InterMedium();
        font-size: 2.1rem;
        line-height: 2.5rem;
        color: var(--black_2);
        background: var(--white_2);
        border-radius: 50%;
        will-change: color, background;
        // transition: color 0.3s ease-in-out, background 0.3s ease-in-out;
      }

      @media (min-width: 1024px) {
        max-width: 36rem;
        margin-bottom: 4rem;
      }
      
      .services-stages__car-img {
        display: none;

        // &--active {
        //   display: block;
        // }
      }

      .services-stages__card-text {
        height: 0;
        background: var(--white_2);
        border-radius: 0.5rem;

        &--active {
          height: auto;
        }

        h3 {
          position: absolute;
          z-index: 10;
          top: 2rem;
          left: 1.5rem;
          width: 67%;
          margin-bottom: 1.5rem;
          @include InterMedium();
          font-size: 2rem;
          line-height: 120%;
          color: var(--black_2);
          will-change: color;
          transition: color 0.3s ease-in-out;
        }

        .services-stages__card-txt {
          position: relative;
          position: relative;
          transform: translateY(41%);
          z-index: 1;
          // transform: translateY(-150%);
          // z-index: -1;
          width: 31rem;
          padding: 1.5rem 1rem 6rem 1.5rem;
          @include InterRegular();
          font-size: 1.6rem;
          line-height: 130.02%;
          color: var(--black_1);

          &--active {
            z-index: 0;
            transform: translateY(0);
          }
        }
      }

      &:hover,
      &:focus {
        // height: 100%;

        &::before {
          z-index: 10;
        }

        &::after {
          color: var(--white_1);
          background: var(--red_1);
        }

        // p {
        //   z-index: 0;
        //   transform: translateY(-5%);
        // }

        .services-stages__card-text {
          // height: auto;

          h3 {
            color: var(--white_1);
          }
        }

        .services-stages__car-img {
          display: block;
        }
      }


      
    }

